.elementor-widget-wcf--video-box-slider .wcf-popup-btn {
  display: inline-flex;
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1;
}
.elementor-widget-wcf--video-box-slider .thumb img {
  width: 100%;
  object-fit: cover;
}
.elementor-widget-wcf--video-box-slider .content {
  padding: 30px 0;
}
.elementor-widget-wcf--video-box-slider.wcf-thumb-yes .thumb {
  position: relative;
}
.elementor-widget-wcf--video-box-slider.wcf-thumb-yes .thumb:after {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  background-color: #000;
  z-index: -1;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: 0.3s ease-in;
}
.elementor-widget-wcf--video-box-slider.wcf-thumb-yes.thumb-after-hover-slide .thumb:after {
  opacity: 0;
  visibility: hidden;
}
.elementor-widget-wcf--video-box-slider.wcf-thumb-yes.thumb-after-hover-slide .swiper-slide:hover .thumb:after {
  opacity: 1;
  visibility: visible;
}
.elementor-widget-wcf--video-box-slider.wcf-thumb-yes.thumb-after-active-slide .thumb:after {
  opacity: 0;
  visibility: hidden;
}
.elementor-widget-wcf--video-box-slider.wcf-thumb-yes.thumb-after-active-slide .swiper-slide.swiper-slide-active .thumb:after {
  opacity: 1;
  visibility: visible;
}
.elementor-widget-wcf--video-box-slider.popup-button-hover-slide .wcf-popup-btn {
  opacity: 0;
  visibility: hidden;
}
.elementor-widget-wcf--video-box-slider.popup-button-hover-slide .swiper-slide:hover .wcf-popup-btn {
  opacity: 1;
  visibility: visible;
}
.elementor-widget-wcf--video-box-slider.popup-button-active-slide .wcf-popup-btn {
  opacity: 0;
  visibility: hidden;
}
.elementor-widget-wcf--video-box-slider.popup-button-active-slide .swiper-slide.swiper-slide-active .wcf-popup-btn {
  opacity: 1;
  visibility: visible;
}
.elementor-widget-wcf--video-box-slider .swiper-slide {
  transition: all 0.3s ease;
  transform: scale(var(--scale-x, 1), var(--scale-y, 1));
}
.elementor-widget-wcf--video-box-slider .swiper-slide.swiper-slide-active {
  transform: scale(1) !important;
}